import { Table } from "antd";
import dayjs from "dayjs";
import React, { useEffect, useState } from "react";
import StockDatePicker from "../../../component/stock-date-picker";
import { api, defaultAxios } from "../../../environment/api";
import errorNotification from "../../../utils/errorNotification";
import DownloadButton from "../downloadButton";

const OrderTable = () => {
  const [orederData, setOrderData] = useState([]);
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(20);
  const [totalSize, setTotalSize] = useState(0);
  const [stockId, setStockId] = useState();
  const [startTime, setStartTime] = useState();
  const [endTime, setEndTime] = useState();
  useEffect(() => {
    defaultAxios({
      url: api.getDisplay.url,
      method: api.getDisplay.method,
      params: {
        page: { page: page, pageSize: pageSize },
        stockId,
        createdTime: { min: startTime, max: endTime },
      },
    })
      .then((res) => {
        setOrderData(res.data.content);
        setTotalSize(res.data.totalSize);
      })
      .catch((err) => {
        errorNotification(err?.response?.data);
      });
  }, [page, pageSize, stockId, startTime, endTime]);

  return (
    <div>
      <StockDatePicker
        onStockChange={(e) => {
          setStockId(e);
        }}
        onStartChange={(e) => {
          setStartTime(e);
        }}
        onEndChange={(e) => {
          setEndTime(e);
        }}
      />
      <DownloadButton
        type="order"
        stockId={stockId}
        startTime={startTime}
        endTime={endTime}
      />
      <Table
        columns={[
          {
            title: " ID",
            dataIndex: "orderId",
            render: (data) => <span>{data || "NULL"}</span>,
            key: Math.random(),
          },
          {
            title: " ID",
            dataIndex: "investorId",
            key: Math.random(),
          },
          {
            title: " ID",
            dataIndex: "stockId",
            key: Math.random(),
          },
          {
            title: "",
            dataIndex: "method",
            render: (data) => <span>{data ? "sell" : "buy"}</span>,
            key: Math.random(),
          },
          {
            title: "",
            dataIndex: "subMethod",
            render: (data) => (
              <span>{data ? "UPDATE" : data === null ? "NULL" : "CANCEL"}</span>
            ),
            key: Math.random(),
          },
          {
            title: "",
            dataIndex: "price",
            key: Math.random(),
          },
          {
            title: "",
            dataIndex: "quantity",
            key: Math.random(),
          },
          {
            title: "",
            dataIndex: "priceType",
            render: (data) => <span>{data ? "LIMIT" : "MARKET"}</span>,
            key: Math.random(),
          },
          {
            title: "",
            dataIndex: "status",
            render: (data) => <span>{data ? "FULL" : "PARTIAL"}</span>,
            key: Math.random(),
          },
          {
            title: "",
            dataIndex: "timeRestriction",
            render: (data) => (
              <span>{data ? "IOC" : data === 2 ? "FOK" : "ROD"}</span>
            ),
            key: Math.random(),
          },
          {
            title: "",
            dataIndex: "createdTime",
            width: 200,
            render: (data) => (
              <span>{dayjs(data).format("YYYY/MM/DD HH:mm:ss")}</span>
            ),
            key: Math.random(),
          },
        ]}
        dataSource={orederData}
        pagination={{
          pageSize: pageSize,
          total: totalSize,
          onChange: (page) => {
            setPage(page);
          },
          onShowSizeChange: (cur, size) => {
            setPageSize(size);
          },
        }}
        sticky
      />
    </div>
  );
};

export default OrderTable;
