import React, { useEffect } from "react";
import { Modal, Form, Input, Select } from "antd";
import { defaultAxios, api } from "../../environment/api";
import errorNotification from "../../utils/errorNotification";

const Create = ({ visible, setVisible, defaultValue, reset, role = [] }) => {
  const [form] = Form.useForm();
  useEffect(() => {
    if (defaultValue) {
      form.setFieldsValue(defaultValue);
    }
  }, [defaultValue]);
  return (
    <Modal
      title={defaultValue ? "" : ""}
      visible={visible}
      onOk={() => {
        form.submit();
      }}
      onCancel={() => {
        form.resetFields();
        setVisible(false);
      }}
      okText=""
      cancelText=""
    >
      <Form
        name="basic"
        layout="vertical"
        form={form}
        onFinish={(value) => {
          const { restApiTime, ...other } = value;
          if (defaultValue) {
            defaultAxios({
              url: api.putInvestor.url,
              method: api.putInvestor.method,
              data: {
                id: defaultValue.id,
                restApiTime: parseInt(restApiTime),
                ...other,
              },
            })
              .catch((err) => {
                errorNotification(err?.response?.data);
              })
              .finally(() => {
                reset(Math.random());
                form.resetFields();
                setVisible(false);
              });
          } else {
            defaultAxios({
              url: api.postInvestor.url,
              method: api.postInvestor.method,
              data: {
                restApiTime: parseInt(restApiTime),
                ...other,
              },
            })
              .catch((err) => {
                errorNotification(err?.response?.data);
              })
              .finally(() => {
                reset(Math.random());
                form.resetFields();
                setVisible(false);
              });
          }
        }}
        initialValues={{ remember: true }}
      >
        <Form.Item
          label=""
          name="account"
          rules={[{ required: true, message: "" }]}
        >
          <Input placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="password"
          rules={[{ required: !defaultValue, message: "" }]}
        >
          <Input placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="restApiTime"
          rules={[
            { required: defaultValue ? true : false, message: "" },
          ]}
          style={{ display: defaultValue ? undefined : "none" }}
        >
          <Input type="number" placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="roleId"
          rules={[{ required: defaultValue ? true : false, message: "" }]}
          style={{ display: defaultValue ? undefined : "none" }}
        >
          <Select
            placeholder=""
            options={role?.map((val) => ({ value: val.id, label: val.name }))}
          />
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default Create;
