import React, { useEffect, useState } from "react";
import { defaultAxios, api } from "../../environment/api";
import errorNotification from "../../utils/errorNotification";
import { Table, Button, Row, Col, Modal, Input, Typography } from "antd";
import {
  EditOutlined,
  DeleteOutlined,
  ExclamationCircleOutlined,
} from "@ant-design/icons";
import Create from "./create";
import dayjs from "dayjs";

const { confirm } = Modal;

const { Title } = Typography;
const RoleManagement = () => {
  const [roleData, setRoleData] = useState([]);
  const [permissionData, setPermissionData] = useState([]);
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(20);
  const [totalSize, setTotalSize] = useState(0);
  const [visible, setVisible] = useState(false);
  const [reset, setReset] = useState(0);
  const [editValue, setEditValue] = useState();
  const [checked, setChecked] = useState([]);
  const [searchCondition, setSearchCondition] = useState({});

  function showDeleteConfirm(id) {
    confirm({
      title: "?",
      icon: <ExclamationCircleOutlined />,
      content: "",
      okText: "",
      okType: "danger",
      cancelText: "",
      onOk() {
        defaultAxios({
          url: api.deleteRole.url,
          method: api.deleteRole.method,
          data: {
            id,
          },
        })
          .catch((err) => {
            errorNotification(err?.response?.data);
          })
          .finally(() => {
            setChecked([]);
            setReset(Math.random());
          });
      },
    });
  }

  useEffect(() => {
    defaultAxios({
      url: api.getRolePermission.url,
      method: api.getRolePermission.method,
    })
      .then((res) => {
        console.log(res.data);
        setPermissionData(res.data);
      })
      .catch((err) => {
        errorNotification(err?.response?.data);
      });
  }, []);

  useEffect(() => {
    defaultAxios({
      url: api.getRole.url,
      method: api.getRole.method,
      params: {
        ...searchCondition,
        page: { page, pageSize },
      },
    })
      .then((res) => {
        console.log(res.data);
        setRoleData(res.data.content);
        setTotalSize(res.data.totalSize);
      })
      .catch((err) => {
        errorNotification(err?.response?.data);
      });
  }, [page, pageSize, reset]);

  const rowSelection = {
    selectedRowKeys: checked,
    onChange: (selectedRowKeys, selectedRows) => {
      setChecked(selectedRows.map((deta) => deta.id));
    },
    preserveSelectedRowKeys: false,
  };
  return (
    <div className="px-10">
      <Create
        visible={visible}
        setVisible={setVisible}
        reset={setReset}
        defaultValue={editValue}
        permission={permissionData}
      />
      <Title level={5}></Title>
      <Row
        className="mb-4 border border-gray-200 p-4"
        justify="space-around"
        align="bottom"
      >
        <Col span={6}>
          
          <Input
            placeholder=""
            size="middle"
            onChange={(e) => {
              setSearchCondition({
                ...searchCondition,
                name: e.target.value,
              });
            }}
          />
        </Col>
        <Col span={2}>
          <Button
            onClick={() => {
              setReset(Math.random());
            }}
          >
            
          </Button>
        </Col>
      </Row>
      <div className="flex justify-end my-5">
        <Button
          disabled={!checked.length}
          type="default"
          className="mr-4"
          onClick={() => {
            showDeleteConfirm(checked);
          }}
        >
          
        </Button>
        <Button
          type="primary"
          onClick={() => {
            setEditValue(null);
            setVisible(true);
          }}
        >
          
        </Button>
      </div>
      <Table
        rowKey="id"
        rowSelection={{
          type: "checkbox",
          ...rowSelection,
        }}
        columns={[
          {
            title: "",
            dataIndex: "name",
          },
          {
            title: "",
            dataIndex: "totalApiTime",
          },
          {
            title: "",
            dataIndex: "permissions",
            render: (data) => (
              <span className="whitespace-pre-line">
                {data.reduce((prev, curr) => prev + curr.id + "\n", "")}
              </span>
            ),
          },
          {
            title: "",
            dataIndex: "createdTime",
            width: 200,
            render: (data) => (
              <span>{dayjs(data).format("YYYY/MM/DD HH:mm:ss")}</span>
            ),
          },
          {
            title: "",
            dataIndex: "updatedTime",
            width: 200,
            render: (data) => (
              <span>{dayjs(data).format("YYYY/MM/DD HH:mm:ss")}</span>
            ),
          },
          {
            title: "",
            dataIndex: "action",
            render: (_, record) => (
              <span>
                <Button
                  type="link"
                  shape="circle"
                  className="inline-flex justify-center items-center mr-2"
                  icon={<EditOutlined />}
                  onClick={() => {
                    setEditValue({
                      id: record.id,
                      name: record.name,
                      totalApiTime: record.totalApiTime,
                      permissions: record.permissions.map((data) => data.id),
                    });
                    setVisible(true);
                  }}
                />
                <Button
                  type="link"
                  shape="circle"
                  className="inline-flex justify-center items-center"
                  onClick={() => showDeleteConfirm([record.id])}
                  icon={<DeleteOutlined />}
                />
              </span>
            ),
          },
        ]}
        dataSource={roleData}
        pagination={{
          pageSize: pageSize,
          total: totalSize,
          onChange: (page) => {
            setPage(page);
          },
          onShowSizeChange: (cur, size) => {
            setPageSize(size);
          },
        }}
        sticky
      />
    </div>
  );
};

export default RoleManagement;
