import React, { useEffect } from "react";
import { Modal, Form, Input, Select } from "antd";
import { defaultAxios, api } from "../../environment/api";
import errorNotification from "../../utils/errorNotification";

const Create = ({
  visible,
  setVisible,
  defaultValue,
  reset,
  permission = [],
}) => {
  const [form] = Form.useForm();
  useEffect(() => {
    console.log(defaultValue, "de");
    if (defaultValue) {
      form.setFieldsValue(defaultValue);
    }
  }, [defaultValue]);
  return (
    <Modal
      title={defaultValue ? "" : ""}
      visible={visible}
      onOk={() => {
        form.submit();
      }}
      onCancel={() => {
        form.resetFields();
        setVisible(false);
      }}
      okText=""
      cancelText=""
    >
      <Form
        name="basic"
        layout="vertical"
        form={form}
        onFinish={(value) => {
          if (defaultValue) {
            defaultAxios({
              url: api.putRole.url,
              method: api.putRole.method,
              data: { ...value, id: defaultValue.id },
            })
              .catch((err) => {
                errorNotification(err?.response?.data);
              })
              .finally(() => {
                reset(Math.random());
                form.resetFields();
                setVisible(false);
              });
          } else {
            defaultAxios({
              url: api.postRole.url,
              method: api.postRole.method,
              data: value,
            })
              .catch((err) => {
                errorNotification(err?.response?.data);
              })
              .finally(() => {
                reset(Math.random());
                form.resetFields();
                setVisible(false);
              });
          }

          console.log(value);
        }}
        initialValues={{ remember: true }}
      >
        <Form.Item
          label=""
          name="name"
          rules={[{ required: true, message: "" }]}
        >
          <Input placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="totalApiTime"
          rules={[
            { required: true, message: "" },
            {
              validator: (_, value) => {
                if (value > 0) return Promise.resolve();
                return Promise.reject("1");
              },
            },
          ]}
        >
          <Input type="number" placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="permissions"
          rules={[{ required: true, message: "" }]}
        >
          <Select
            mode="multiple"
            placeholder=""
            options={permission.map((val) => ({ value: val, label: val }))}
          />
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default Create;
