import React, { useEffect } from "react";
import { Modal, Form, Input, InputNumber, Select } from "antd";
import { api, defaultAxios } from "../../environment/api";
import errorNotification from "../../utils/errorNotification";
const { Option } = Select;

const CreateAndEdit = ({ isVisible, setIsVisible, initVal, reload }) => {
  const [form] = Form.useForm();
  console.log(initVal);
  useEffect(() => {
    if (initVal) {
      form.setFieldsValue(initVal);
    }
  }, [initVal]);
  return (
    <Modal
      title={`${initVal ? "" : ""}`}
      visible={isVisible}
      cancelText=""
      okText=""
      onCancel={() => {
        form.resetFields();
        setIsVisible(false);
      }}
      onOk={() => {
        form.submit();
      }}
    >
      <Form
        form={form}
        layout="vertical"
        onFinish={(value) => {
          console.log(value);
          const axiosType = initVal ? "putStock" : "postStock";
          console.log({
            url: api[axiosType].url,
            method: api[axiosType].method,
          });
          defaultAxios({
            url: api[axiosType].url,
            method: api[axiosType].method,
            data: { ...value, groupId: [] },
          })
            .then(() => {
              form.resetFields();
              setIsVisible(false);
              reload(Math.random());
            })
            .catch((err) => {
              errorNotification(err?.response?.data);
            });
        }}
      >
        <Form.Item
          label="ID"
          name="id"
          rules={[{ required: true, message: "Require" }]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label=""
          name="closedPrice"
          rules={[{ required: true, message: "Require" }]}
        >
          <InputNumber type="number" className="w-full" min={0} />
        </Form.Item>
        <Form.Item
          label="(%)"
          name="priceLimit"
          rules={[{ required: true, message: "Require" }]}
        >
          <InputNumber type="number" className="w-full" min={0} />
        </Form.Item>
        <Form.Item
          label=""
          name="currentPrice"
          rules={[{ required: true, message: "Require" }]}
        >
          <InputNumber type="number" className="w-full" min={0} />
        </Form.Item>
        <Form.Item
          label=""
          name="type"
          rules={[{ required: true, message: "Require" }]}
        >
          <Select className="w-full">
            <Option value={0}></Option>
            <Option value={1}>ETF</Option>
            <Option value={2}></Option>
          </Select>
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default CreateAndEdit;
